<template>

	<div>
		<div class="caldera-config-group">
		<label for="cf-translations-language">
				{{labelText}}
			</label>
			<div class="caldera-config-field">
				<select
					class="block-input"
					id="cf-translations-language"
					v-model="language"
				>
					<option></option>
					<option
						v-for="language in formLanguages"
						v-bind:value="language.code"
					>
						{{language.name}}
					</option>
				</select>
			</div>
		</div>
		<div>
			<div class="caldera-config-group">
				<label for="cf-translations-languages">
					{{labelTextAdd}}
				</label>
				<div class="caldera-config-field">
					<select
							class="block-input"
							v-model="langToAdd"
							id="cf-translations-languages"
					>
						<option></option>
						<option
								v-for="language in languages"
								v-bind:value="language.code"
						>
							{{language.name}}
						</option>
					</select>
				</div>
			</div>
			<div class="caldera-config-group" v-if="'false' != langToAdd">
				<button @click="addLang">
					Add Language To Form Translations?
				</button>
			</div>
		</div>
	</div>


</template>
<script>
	import { mapState } from 'vuex';

	import { formState } from './states/formState';
	import { formLanguagesState } from './states/formLanguagesState';
	import { languagesState } from './states/languagesState';
	import { languageState } from './states/languageState';

	export default{
		data(){
			return {
				//labelText: this.$store.getters.strings.choose_lang,
				labelText: 'Choose Lanaugage',
				labelTextAdd: 'ADD Lanaugage',
				langToAdd: 'false',
			}
		},
		mounted(){
		},
		computed: {
			formLanguages: formLanguagesState,
			language: languageState,
			languages: languagesState,
			form: formState,
		},
		watch: {
			language(val){
				this.$store.commit( 'language', val );
			}
		},
		methods: {
			showAddLang(){
				return this.langToAdd ? true : false;
			},
			addLang(ev){
				this.$store.dispatch( 'addLanguage', {
					language: this.langToAdd,
					form: this.form.ID
				}).then( r => {
					this.formLanguages = this.form.languages_named;
					this.$store.commit( 'language', this.langToAdd );
					this.langToAdd = 'false';
				})
			}
		}
	}
</script>


