<template>
	<div class="caldera-config-group">
		<label for="cf-translations-form">
			{{labelText}}
		</label>
		<div class="caldera-config-field">
			<select
				class="block-input"
				id="cf-translations-form"
				v-model="form"
			>
				<option></option>
				<option
						v-for="form in forms"
						v-bind:value="form.ID"
				>
					{{form.name}}
				</option>
			</select>
		</div>
	</div>

</template>
<script>
	import { mapState } from 'vuex'
	export default{
		data(){
			return {
				form: '',
				//labelText: this.$store.getters.strings.choose_form,
				labelText: 'Fix This Label',
			}
		},
		computed: mapState({
			forms: state => state.forms,
		}),
		watch: {
			form(val){
				this.$store.dispatch( 'form', val );
			}
		}
	}
</script>


