/**
 * @param {String} getter The name of the getter
 * @param {String|undefined} mutation The name of the mutation, or undefined to use same name as getter
 */
export  const stateFactory = ( getter, mutation ) => {
	if( ! mutation ){  mutation = getter; }

	return {
		get ()  {
			return this.$store.state[ getter ];
		},
		set (value)  {
			this.$store.commit(mutation, value)
		}
	}
};



