import Vue from 'vue'
import SettingsView from  './views/Settings.vue';

import Promise from './promise-polyfill';
import store from  './store/index';
const mainSettingsApp = new Vue({
	el: '#cf-translations-app',
	store,
	components: {
		'settings': SettingsView
	},
	render(h) {
		return h(
			'div',
			{
				attrs: {
					id: 'cf-translations'
				}
			},
			[
				h( 'settings')
			]
		)
	}

});



