<template>

	<div class="caldera-config-group">
		<label for="cf-translations-field">
			{{labelText}}
		</label>
		<div class="caldera-config-field">
			<select
					id="cf-translations-field"
					v-model="fieldId"
			>
				<option></option>
				<option
						v-for="_field in fields"
						v-bind:value="_field.ID"
				>
					{{_field.label}}
				</option>
			</select>
		</div>
	</div>

</template>
<script>
	import { mapState } from 'vuex'

	export default{
		data(){
			return {
				fieldId: '',
				labelText: 'Choose Field To Translate',
			}
		},
		computed: mapState({
			fields: state => state.form.fields[state.language],
		}),
		watch: {
			fieldId(val){
				this.$store.commit( 'fieldId', val );
			}

		}
	}
</script>


