
import maybeAddField from './util/maybeAddField';


export const GETTERS = {
	fieldId : state => {
		return state.hasOwnProperty( 'fieldId' ) && undefined != state.fieldId ? state.fieldId : false;
	},
	strings : state => {
		return state.data.strings;
	},
	data : state => {
		return state.data;
	},
	local : state => {
		return state.local;
	},
	form : state => {
		return state.form;
	},
	formId : state => {
		return state.form.ID;
	},
	forms : state => {
		return state.forms;
	},
	languages : state => {
		return state.languages;
	},
	language : state => {
		return undefined != state.language ? state.language : '';
	},
	fields: state => {
		return state.forms.fields[state.language];

	},
	field: state => {
		if( 'object' != typeof  state.field ){
			return {};
		}
		if( state.field.hasOwnProperty('option') ){
			Object.getOwnPropertyNames( state.field.option ).forEach( opt => {
				state.field.option[opt] ={
					opt: opt,
					label: state.field.option[opt]
				}
			});

		}
		return state.field;
	},
	formLanguages: state => {
		return state.formLanguages;

	}
};



