<template>

	<div>
		<div class="cf-translate-left">
			<div class="caldera-config-group">
				<label for="cf-translations-label">
					{{labelText.label}}
				</label>
				<div class="caldera-config-field">
					<input
						id="cf-translations-label"
						v-model="theField.label"
					>
				</div>
			</div>

			<div class="caldera-config-group">
				<label for="cf-translations-description">
					{{labelText.description}}
				</label>
				<div class="caldera-config-field">
					<input
						id="cf-translations-description"
						v-model="theField.description"
					>
				</div>
			</div>

			<div class="caldera-config-group">
				<label for="cf-translations-theDefault">
					{{labelText.theDefault}}
				</label>
				<div class="caldera-config-field">
					<input
						id="cf-translations-theDefault"
						v-model="theField.default"
					/>
				</div>
			</div>
		</div>
		<div class="cf-translate-right">
			<div
				v-if="hasOptions"
				v-for="opt in field.option"
			>

				<div class="caldera-config-group">
					<label
						v-bind:for="'cf-translations-' + opt.code"
					>
						{{opt.label}}
					</label>
					<div class="caldera-config-field">
						<input
							v-id:for="'cf-translations-' + opt.code"										v-model="theField.option[opt]"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>


</template>
<script>
	import { mapState } from 'vuex'
	export default{
		data(){
			return {
				ID: this.fieldId,
				labelText: {
					label: 'Field Label'	,
					description: 'Feild Description'	,
					theDefault: 'Feild theDefault'	,

				},
				//labelText: this.$store.get.strings.choose_lang,
				hasOptions: false,
				theField: {}
			}
		},
		mounted()  {
			this.theField = this.field;
			this.hasOptions = null !== this.field.option;
		},
		computed: mapState({
			field: state => state.field,
		}),
		watch : {
			theField(val){
				this.$store.commit('fieldValue', this.theField);
			},

		}
	}
</script>


